<template>
  <div class="enrollments">
    <div class="content">
      <el-col :span="8">
        <el-card shadow="always" >
          <el-divider>
            <p>
            <i class="el-icon-star-off" style="font-size:18px"></i>
            欢迎登录星迹
            <i class="el-icon-star-off" style="font-size:18px"></i>
            </p>
          </el-divider>
          <el-form ref="selectForm" :model="selectForm" :rules="rules"  label-width="130px" >
            <el-form-item label="用户名" prop="username">
              <!--<el-select v-model="selectForm.students" filterable placeholder="少侠如何称呼?">-->
                <!--<el-option-->
                  <!--v-for="item in students"-->
                  <!--:key="item.Id"-->
                  <!--:label="item.Name"-->
                  <!--:value="item.Id"-->
                <!--&gt;</el-option>-->
              <!--</el-select>-->
              <el-input type="text" v-model="selectForm.username" placeholder="" style="width: 188px" required></el-input>
            </el-form-item>
            <!--<el-divider content-position="center">欲上青天揽明月</el-divider>-->
            <el-form-item label="密码" prop="password">
              <!--<el-select v-model="selectForm.courses" filterable placeholder="请输入你的密码">-->
                <!--<el-option-->
                  <!--v-for="item in courses"-->
                  <!--:key="item.Id"-->
                  <!--:label="item.Title"-->
                  <!--:value="item.Id"-->
                <!--&gt;</el-option>-->
              <!--</el-select>-->
              <el-input type="passward" v-model="selectForm.password" auto-complete="off" style="width: 188px" required></el-input>
            </el-form-item>

            <el-form-item label="身份" prop="level">
              <el-select v-model="selectForm.level" filterable placeholder="请选择您的登录身份" style="width: 188px" required>
                <el-option
                  v-for="item in level"
                  :key="item.value"
                  :label="item.level"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>

            <el-form-item>
              <el-button type="primary" @click="onSubmit('selectForm')">登录</el-button>
              <el-button @click="resetForm('selectForm')">重填</el-button>
            </el-form-item>

          </el-form>
          <div style="text-align: center">
          <span v-on:click="ToRegister">没有账号？马上注册</span>
          </div>
        </el-card>
      </el-col>
    </div>
  </div>
</template>
<script>
import qs from "qs";
export default {
  name: "enrollments",
  data() {
    return {
      name: "liudaxia",
      username: [],
      password: [],
      level: [
        { level: "学生", value: 0 },
        { level: "教师", value: 1 },
        { level: "管理员", value: 2 }
      ],
      selectForm: {
        username: "",
        password: "",
        level: ""
      },
      rules: {
        username: [
          { required: true, message: "请输入宝贝的名字", trigger: "change" },
          {min: 2, max: 10, message: "长度为2到10个字符", trigger: "blur"}

        ],
        password: [
          {
            required: true,
            message: "请输入你的密码",
            trigger: "change"
          }
        ],
        level: [
          { required: true, message: "请确认你的身份", trigger: "change" },
        ]
      }
    };
  },
  methods: {
    onSubmit(formName) {
      // if (this.selectForm.usernmae === "") {
      //   return this.$message({
      //     message: "请输入宝贝的名字",
      //     type: "warning"
      //   });
      // }
      // if (this.selectForm.password === "") {
      //   return this.$message({
      //     message: "请输入您的密码！",
      //     type: "warning"
      //   });
      // }
      // if (this.selectForm.level === "") {
      //   return this.$message({
      //     message: "选择您的身份",
      //     type: "warning"
      //   });
      // }
      this.$refs[formName].validate(valid => {
        if (valid) {
          var strData = {
            stuUsername: this.selectForm.username,
            stuPassword: this.selectForm.password,
          };

          //学生登录,
          // 分别把下面的url换成各自对应的接口就好
          if (this.selectForm.level == 0) {
            this.axios
              .post("http://localhost:8081/stu/login", qs.stringify(strData))
              .then(result => {
                if (result.data == 'success') {
                  this.$notify({
                    id: "",
                    title: "登录成功(*￣︶￣)，",
                    message: "登录完成！",
                    position: 'top-left',
                    type: "success"
                  });
                  this.$root.levelid = this.selectForm.level;
                  this.$router.push("/students");
                }
                else if(result.data==='failure'){
                  this.$message({
                    message: "登录失败o(╥﹏╥)o",
                    type: "danger"
                  });
                }
              })
              .catch(err => {
                // this.$root.levelid = this.selectForm.level;
                // console.log(this.$root.levelid);
                this.$router.push("/students");
                this.$message({
                  message: "登录失败o(╥﹏╥)o",
                  type: "danger"
                });
              });
          }
          //老师登录
          else if (this.selectForm.level == 1) {
            this.axios
              .post("http://localhost:8081/tea/login", qs.stringify(strData))
              .then(result => {
                if (result.data === 'success') {
                  this.$notify({
                    id: "",
                    title: "登录成功(*￣︶￣)，",
                    message: "登录完成！",
                    position: 'top-left',
                    type: "success"
                  });
                  this.$root.levelid = this.selectForm.level;
                  this.$router.push("/coach");
                }
                else if(result.data==='failure'){
                  this.$message({
                    message: "登录失败o(╥﹏╥)o",
                    type: "danger"
                  });
                }
              })
              .catch(err => {
                //this.$root.levelid = this.selectForm.level;
                this.$router.push("/coach");
                //console.log(this.$root.levelid)
                this.$message({
                  message: "登录失败o(╥﹏╥)o",
                  type: "danger"
                });
              });

          }
          //管理员登录
          else {
            this.axios
              .post("http://localhost:8004/admim/login", qs.stringify(strData))
              .then(result => {
                if (result.data == 'success') {
                  this.$notify({
                    id: "",
                    title: "登录成功(*￣︶￣)，",
                    message: "登录完成！",
                    position: 'top-left',
                    type: "success"
                  });
                  this.$root.levelid = this.selectForm.level;
                  this.$router.push("/admin");
                }
                else if(result.data==='failure'){
                  this.$message({
                    message: "登录失败o(╥﹏╥)o",
                    type: "danger"
                  });
                }
              })
              .catch(err => {
                //this.$root.levelid = this.selectForm.level;
                // console.log(this.$root.levelid);
                this.$router.push("/admin");
                this.$message({
                  message: "登录失败o(╥﹏╥)o",
                  type: "danger"
                });
              });
          }
        }
      });
    },

    ToRegister(){
      this.$router.push('/register')
    },
    // 重置表单
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },

  },
  created() {
    console.log(this.$root.levelid);

  }
};
</script>
<style lang="less" scoped>
.enrollments {
  background-image: url("/static/images/bgnezha.jpg");
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  min-width: 1000px;
  z-index: -10;
  zoom: 1;
  background-color: rgba(0, 0, 0, 0.5);
  background-repeat: repeat;
  background-size: cover;
  -webkit-background-size: cover;
  -o-background-size: cover;
  background-position: center 0;
  .content {
    display: flex;
    // 调整选课框位置
    margin: 10% 0 0 0;
    justify-content: center;
    overflow: hidden;
  }
  span{cursor:pointer;text-align: center;}
  span:hover{color:#41b883;}

}
</style>



